.content .offers .card_payment_box {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  right: 0;
  background: #00000070;
  z-index: 3;
}
.content .offers .card_payment_box .form {
  position: fixed;
  display: flex;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
  flex-direction: column;
  padding: 20px;
  z-index: 3;
  width: 500px;
}
.content .offers .card_payment_box .form .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .offers .card_payment_box .form .top span.title {
  font-size: 16px;
  color: #293442;
  margin-bottom: 10px;
  font-weight: 600;
  direction: ltr;
}
.content .offers .card_payment_box .form .top span.title #this_price {
  margin-right: 2px;
  color: #128c7e;
  padding-top: 1px;
}
.content .offers .card_payment_box .form .top span.close-box {
  box-sizing: content-box;
  width: 1em;
  height: 1em;
  color: #000;
  background: transparent url(/static/img/close.svg) center/1em auto no-repeat;
  border: 0;
  border-radius: 0.25rem;
  opacity: 0.5;
  cursor: pointer;
}
.content .offers .card_payment_box .form .center .form-group label {
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 500;
  margin-top: 15px;
}
.content .offers .card_payment_box .form .center .form-group .input_group {
  display: flex;
  flex-direction: row;
}
.content .offers .card_payment_box .form .center .form-group .input_group .icon {
  width: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.375rem 0.75rem;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  text-align: center;
  white-space: nowrap;
  background-color: #e9ecef;
  border: 1px solid #ced4da;
  border-radius: 0 8px 8px 0;
  color: #128c7e;
}
.content .offers .card_payment_box .form .center .form-group .input_group input {
  border-radius: 8px 0 0 8px;
  padding: 10px 18px;
  color: #24292e;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  margin: 0;
  border: 0.5px solid #ced4da;
  outline: 0;
}
.content .offers .card_payment_box .form .center .bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .offers .card_payment_box .form .center .bottom .form_group_input label {
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 500;
  margin-top: 15px;
}
.content .offers .card_payment_box .form .center .bottom .form_group_input .input_group {
  display: flex;
}
.content .offers .card_payment_box .form .center .bottom .form_group_input .input_group #month {
  border-radius: 0px 8px 8px 0;
  padding: 10px 18px;
  color: #24292e;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  margin: 0;
  border: 0.5px solid #ced4da;
  outline: 0;
  width: 110px;
}
.content .offers .card_payment_box .form .center .bottom .form_group_input .input_group #year {
  border-radius: 8px 0 0 8px;
  padding: 10px 18px;
  color: #24292e;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  margin: 0;
  border: 0.5px solid #ced4da;
  outline: 0;
  width: 110px;
}
.content .offers .card_payment_box .form .center .bottom .CVV {
  display: flex;
  flex-direction: column;
}
.content .offers .card_payment_box .form .center .bottom .CVV label {
  margin-right: 1px;
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 500;
  margin-top: 15px;
}
.content .offers .card_payment_box .form .center .bottom .CVV #CVV {
  border-radius: 8px;
  padding: 10px 18px;
  color: #24292e;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  margin: 0;
  border: 0.5px solid #ced4da;
  outline: 0;
}
.content .offers .card_payment_box .form .center input[type=submit] {
  background-color: #04b07d;
  color: white;
  border-radius: 8px;
  padding: 10px 18px;
  transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  border: 0.5px solid #ced4da;
  outline: 0;
  margin-top: 25px;
}
.content .offers .list {
  text-align: center;
}
.content .offers .list .cont {
  display: inline-block;
  margin: 15px 15px;
}
.content .offers .list .cont .box {
  display: flex;
  flex-direction: column;
  background: white;
  padding: 10px;
  border-radius: 6px;
  align-items: center;
}
.content .offers .list .cont .box .top {
  margin-bottom: 15px;
}
.content .offers .list .cont .box .top span:not(span.and__smal) {
  font-size: 35px;
  font-weight: 700;
  text-align: center;
  display: block;
  color: #40475c;
  direction: ltr;
}
.content .offers .list .cont .box .top .and__smal {
  font-size: 20px;
  margin-left: 1px;
}
.content .offers .list .cont .box .center {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content .offers .list .cont .box .center span:not(span.and__) {
  font-size: 13px;
  color: #9e9e9e;
  border-bottom: 1px solid #9e9e9e3d;
  padding-bottom: 5px;
  margin-bottom: 11px;
  font-weight: bold;
}
.content .offers .list .cont .box .center .and__ {
  color: #04b07d;
}
.content .offers .list .cont .box .bottom {
  margin-top: 10px;
}
.content .offers .list .cont .box .bottom button {
  padding: 8px 50px;
  background: #04b07d;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

@media (max-width: 502px) {
  .offers .list .cont .box {
    width: 285px !important;
    padding: 30px !important;
  }
}
@media (max-width: 1015px) {
  .offers .list .cont {
    margin: 15px 10px !important;
  }
}
@media (min-width: 1200px) {
  .offers .list .cont .box {
    width: 240px !important;
    padding-bottom: 20px !important;
    padding-top: 20px !important;
  }
}
@media (max-width: 550px) {
  .card_payment_box .form {
    width: 95% !important;
  }

  .content .offers .card_payment_box .form .center .bottom .form_group_input .input_group #month,
.content .offers .card_payment_box .form .center .bottom .form_group_input .input_group #year {
    width: 90px !important;
  }

  .content .offers .card_payment_box .form .center .bottom .CVV #CVV {
    width: calc(100% - 20px) !important;
    margin-right: 20px !important;
  }

  .content .offers .card_payment_box .form .center .bottom .CVV label {
    margin-right: 22px !important;
  }
}